import { defineStore } from 'pinia'

export const useDataStore = defineStore('data', {
  state: () => ({
    temps: []
  }),
  getters: {
    // ...
  },
  actions: {
    loadData (api) {
      api.get(
        'monitoring-data.json'
        // 'sample-example.json'
        // 'monitoring-data-old.json'
      ).then(
        res => { this.temps = res.data },
        error => { alert(error); return Promise.reject(error) } // add some notify
      )
    }
  }
})
